import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'order_list',
            component: () => import(/* webpackChunkName: "about" */ './views/order_list.vue')
        },
        {
            path: '/order_add',
            name: 'order_add',
            component: () => import(/* webpackChunkName: "about" */ './components/order_add.vue')
        },
        {
            path: '/today',
            name: 'today',
            component: () => import(/* webpackChunkName: "about" */ './components/today.vue')
        },
        {
            path: '/month',
            name: 'month',
            component: () => import(/* webpackChunkName: "about" */ './components/month.vue')
        },
        {
            path: '/year',
            name: 'year',
            component: () => import(/* webpackChunkName: "about" */ './components/year.vue')
        },
        {
            path: '/vip',
            name: 'vip',
            component: () => import(/* webpackChunkName: "about" */ './components/vip.vue')
        },
    ]
})
